.page-main__title {
  position: relative;

  margin: 0;
  margin-top: 20px;

  font-size: 19px;
  text-transform: uppercase;

  &::after {
    content: "";
    position: absolute;
    bottom: -20px;
    left: 35%;

    width: 30%;
    height: 1px;

    border: 1px solid rgb(187, 187, 187);
  }

  @media (min-width: $desktop) {
    margin-top: 40px;

    font-size: 21px;
  }
}
