.works {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  // padding-bottom: 40px;
}

.works__text {
  text-align: center;
}

.works__list {
  @include list-reset();

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 20px;
  padding: 0 30px;

  @media (min-width: $tablet) {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 50px;
  }

  @media (min-width: $desktop) {
    margin-top: 40px;
  }
}

.works__item {
  flex-shrink: 1;
  width: 100%;
  min-height: 200px;
  margin-bottom: 40px;

  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.4);

  &:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }

  &--technomarkt {
    background-image: url("../img/technomart-preview.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &--mishka {
    background-image: url("../img/mishka-preview.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  @media (min-width: $tablet) {
    width: 270px;
    min-height: 250px;
    // margin-left: 80px;

    &:first-child {
      margin-left: 0;
    }
  }

  @media (min-width: $desktop) {
    width: 400px;
    min-height: 300px;
  }
}

.works__descr {
  margin: 0;
  margin-top: 35px;

  font-size: 14px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}

.works__link {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding-top: 60px;

  text-align: center;
  color: transparent;

  background: transparent;


  &:hover,
  &:focus {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }
}
