.services {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-bottom: 40px;

  background-color: #f5f5f5;
}

.services__list {
  @include list-reset();

  display: flex;
  // flex-direction: column;
  flex-wrap: wrap;
  margin-top: 15px;
  padding: 0 50px;

  @media (min-width: $tablet) {
    flex-direction: row;
    justify-content: space-between;
    padding: 0 80px;
  }

  @media (min-width: $desktop) {
    width: 100%;
    margin-top: 40px;
  }
}

.services__item {
  // display: flex;
  width: 100%;
  margin-bottom: 20px;
  padding: 20px 10px;
  padding-bottom: 20px;

  border: 1px solid rgb(163, 161, 161);

  &:hover,
  &:focus {
    box-shadow: 0 10px 25px rgba(154, 161, 173, 0.5);
  }

  @media (min-width: $tablet) {
    width: 250px;
    // margin-left: 50px;
  }

  @media (min-width: $desktop) {
    width: 200px;
    min-height: 150px;
    // margin-left: 50px;
  }
}

.services__ch {
  margin: 0;
  margin-bottom: 15px;

  font-size: 14px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}

.services__descr {
  margin: 0;
  margin-top: 35px;

  font-size: 14px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}

.services__text {
  margin: 0;
  margin-top: 5px;

  font-size: 13px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 14px;
  }
}
