.scroll-up {
  position: fixed;
  right: 15px;
  bottom: 30px;

  width: 35px;
  height: 35px;
  padding-top: 4px;

  text-align: center;
  color: transparent;

  background-image: url("../img/up-arrow_icon.svg");
  background-repeat: no-repeat;
  background-size: 34px 34px;
}

.scroll-up--show {
  display: block;
}

.scroll-up--hide {
  display: none;
}
