.navigation {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
  padding-top: 15px;

  background-color: white;

  @media (min-width: $tablet) {
    flex-direction: row;
    padding-top: 0;
  }
}

.navigation__toggle {
  position: absolute;
  top: 7px;
  right: 0;
  z-index: 1;

  width: 60px;
  height: 60px;

  font-size: 1px;
  color: transparent;

  background: none;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;

  &--open {
    background-image: url("../img/icon-menu-close.svg");
  }

  &--close {
    background-image: url("../img/icon-menu-open.svg");
  }

  @media (min-width: $tablet) {
    display: none;
  }
}

.navigation__list {
  @include list-reset ();

  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;

  &--show {
    display: flex;
  }

  &--hide {
    display: none;
  }

  @media (min-width: $tablet) {
    display: flex;
    flex-direction: row;
    padding-right: 20px;
  }
}

.navigation__about-me {
  display: inline-block;
  padding-top: 10px;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;

  font-weight: 700;
  font-size: 17px;
  vertical-align: top;
  text-transform: uppercase;

  border-bottom: 1px solid;
  border-bottom-color: rgb(224, 224, 224);

  &:hover,
  &:focus {
    opacity: 0.3;
  }


  @media (min-width: $tablet) {
    border: none;
  }

  @media (min-width: $desktop) {
    padding-top: 15px;

    font-size: 19px;
  }
}

.navigation__item {
  display: flex;
  justify-content: center;
  width: 100%;

  @media (min-width: $tablet) {
    width: 100px;
  }
}

.navigation__link {
  width: 100%;
  padding-top: 15px;
  padding-right: 20px;
  padding-bottom: 15px;
  padding-left: 20px;

  font-weight: 700;
  text-align: right;

  border-bottom: 1px solid;
  border-bottom-color: rgb(224, 224, 224);

  &:hover,
  &:focus {
    opacity: 0.3;
  }

  @media (min-width: $tablet) {
    padding-right: 15px;
    padding-left: 15px;

    border: none;
  }

  @media (min-width: $desktop) {
    font-size: 19px;
  }
}
