.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-right: 80px;
  padding-bottom: 40px;
  padding-left: 80px;

  background-color: #f5f5f5;
}

.skills__list {
  @include list-reset();

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  margin-top: 20px;

  @media (min-width: $tablet) {
    justify-content: space-between;
  }

  @media (min-width: $desktop) {
    margin-top: 40px;
  }
}

.skills__item {
  position: relative;

  display: flex;
  flex-direction: column;
  align-items: center;
  width: 120px;
  height: 120px;
  // margin-left: 50px;
  margin-bottom: 50px;
  padding: 10px;

  border: 10px solid rgb(230, 112, 112);
  border-radius: 100%;

  // &::before {
