  // &::after {
  //   content: "";

  //   border-right-color: black;
  //   transform: rotate(45deg);
  // }
}

.skills__ch {
  // order: -1;
  margin: 0;
  margin-top: 20px;
}

.skills__descr {
  margin: 0;
  margin-top: 35px;

  font-size: 14px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}

.skills__count {
  margin: 30px;
}
