.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  padding-bottom: 40px;
}

.contacts__descr {
  margin: 0;
  margin-top: 35px;

  font-size: 14px;
  text-align: center;

  @media (min-width: $desktop) {
    font-size: 15px;
  }
}

.contacts__form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @media (min-width: $desktop) {
    margin-top: 20px;
  }
}

.contacts__btn {
  @include btn-reset();

  margin-top: 20px;

  background-color: rgb(224, 228, 228);

  &:hover,
  &:focus {
    color: white;

    background-color: rgb(131, 133, 133);
  }

  @media (min-width: $desktop) {
    height: 45px;
  }
}

.contacts__item {
  margin-left: 20px;
  padding: 10px;

  border: 1px solid gray;
}

.contacts__text {
  text-align: center;
}

.contacts__label {
  @media (min-width: $desktop) {
    font-weight: 700px;
    font-size: 24px;
  }
}

.contacts__input {
  width: 300px;

  &#message {
    min-height: 100px;
  }

  @media (min-width: $desktop) {
    min-height: 30px;
    margin: 10px auto;
  }
}
